body {
  margin: 0;
}

.card {
  color: #060b35;
  box-sizing: border-box;
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  height: 408px;
  font-family: Roboto, sans-serif;
}

.header__image {
  padding-inline: 19px;
  width: 160px;
  height: 134px;
  padding-top: 32px;
}

.main {
  padding-inline: 16px;
}

.main__product-name {
  text-align: left;
  margin-top: 36px;
  margin-bottom: 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.main__product-code {
  color: #616070;
  text-align: left;
  margin-top: 4px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.reviews {
  text-align: right;
  margin-left: 17px;
  margin-block: 0;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.price {
  justify-content: space-between;
  align-items: center;
  margin-top: 24px;
  display: flex;
}

.price__text {
  color: #616070;
  text-align: left;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.price__value {
  text-align: right;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.button {
  background-color: #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 166px;
  height: 40px;
  margin: 16px;
  display: flex;
}

.button__link {
  color: #fff;
  text-align: center;
  text-transform: uppercase;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
}

.button:hover {
  box-sizing: border-box;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.button:hover .button__link {
  color: #00acdc;
}

.stars {
  justify-content: center;
  align-items: center;
  margin-top: 16px;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars--4 :nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}
